/*
=====
LODEARS
=====
*/
html, body {
    overflow-y: hidden;
    overflow-x: hidden;
}
a {
    text-decoration: none;
    &:hover, &:focus {
        text-decoration: none;
    }
}

.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 230px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #000;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}





  

 
.owl-carousel .owl-item img {
    width: auto;
}






/*------ search ------*/
.search {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.502);
  z-index: 99999;
  .zx-container {
    width: 80%;
    height: 45px;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
  }
  .close {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.602);
    color: #fff;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    button {
      border: none;
      outline: none;
      background-color: transparent;
    }
  }
  input {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    background-color: #fff;
    border-radius: 50px;
    padding: 0 20px;
    &::placeholder {
      font-family: ar-reg;
      font-size: 15px;
    }
  }
  .search-icon {
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    outline: none;
    border: none;
    background-color: transparent;
    color: #000;
    font-size: 18px;
  }
}




/*------ navbar start ------*/
.nav-bar {
  background-color: #fff;
  position: relative;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
  }
  .sec-one {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 15px 25px;
    .social {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin: 0 20px;
      padding: 10px 20px;
      border-right: 1px solid rgb(220, 222, 222);
      a {
        display: block;
        width: 33px;
        height: 33px;
        border-radius: 50%;
        background-color: rgb(218, 218, 218);
        color: rgb(255, 255, 255);
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 2.5px;
        transition: all .5s ease-in-out;
        &:hover {
          background-color: #000;
          color: #fff;
        }
      }
    }
  }
  .sec-two {
    ul {
      height: 100%;
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      a {
        font-family: inherit;
        font-size: inherit;
        color: inherit;
      }
      li {
        // padding: 0 22px;
        width: 130px;
        height: 100%;
        border-left: 1px solid rgb(242, 242, 242);
        text-align: center;
        font-family: ar-reg;
        font-size: 14px;
        color: rgb(0, 0, 0);
        &:last-child {
          border: none;
        }
      }
      .nav-img {
        height: 63px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
  .sec-three {
    height: 100%;
    margin: 0 0 0 50px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 81px;
      height: 100%;
      background-color: rgb(247, 247, 247);
      color: rgb(128, 128, 128);
    }
    button {
      outline: none;
      border: none;
      background-color: transparent;
      font-size: 18px;
    }
    span {
      font-family: ar-reg;
      font-size: 13px;
      padding: 10px 0 0 0;
    }

  }
  .icons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      font-size: 20px;
      color: rgb(255, 255, 255);
      &:nth-of-type(1) {
        background-color: rgb(41, 40, 43);
      }
      &:nth-of-type(2) {
        background-color: rgb(58, 57, 61);
      }
      &:nth-of-type(3) {
        background-color: rgb(109, 107, 115);
      }
    }
    button {
      outline: none;
      border: none;
      background-color: transparent;
    }
  }
}
/*------ navbar end ------*/






/*------ header start ------*/
.header {
  .header-back {
    position: relative;
    overflow: hidden;
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        transform: scale(1.02);
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      .owl-prev, .owl-next {
        outline: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 41.274px;
        color: rgb(255, 255, 255);
        transition: all .5s ease-in-out;
        &:hover {
          color: #000;
        }
      }
      .owl-prev {
        right: 40px;
      }
      .owl-next {
        left: 40px;
      }
    }
    .owl-dots {
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: 40px;
      transform: translateX(-50%);
      .owl-dot {
        width: 9px;
        height: 9px;
        background-color: rgba(0, 0, 0, 0.502);
        border-radius: 50%;
        outline: none;
        margin: 0 2.5px;
        &.active {
          background-color: #fff;
        }
      }
    }
  }
}
/*------ header end ------*/



/*------ servs start ------*/
.servs {
  background-color: rgb(54, 53, 57);
  padding: 24px 0;
  .zx-container {
