/*------ category start ------*/
.category {
  margin: 0 0 0 0;
  background-image: linear-gradient( 0deg, rgb(245,245,245) 0%, rgb(255,255,255) 100%);
  padding: 60px 0 70px 0;
  .zx-container {
    margin: 60px 0 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .category-header {
    text-align: center;
    font-family: ar-md;
    font-size: 22px;
    color: rgb(54, 53, 57);
    margin: 0 0 0 0;
    position: relative;
    &::after {
      content: url("../images/underline-1.png");
      position: absolute;
      bottom: -20px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .cat {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    a {
      transition: all .5s ease-in-out;
      &:first-child {
        width: 185px;
        height: 185px;
        margin: 0 0 15px 0;
        background-color: rgb(109, 107, 115);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
    img {
      transition: all .5s ease-in-out;
      width: auto !important;
    }
    .cat-header {
      font-family: ar-reg;
      font-size: 18px;
      color: rgb(54, 53, 57);
    }
    &:hover {
      a {
        color: #000;
        &:first-child {
          background-color: #363539;
        }
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      display: none;
      .owl-prev, .owl-next {
        outline: none;
        position: absolute;
        top: 40%;
        transform: translateY(-50%);
        font-size: 18px;
        color: rgba(0, 0, 0, 0.4);
        transition: all .5s ease-in-out;
        &:hover {
          color: rgb(0, 0, 0);
        }
      }
      .owl-prev {
        right: -45px;
      }
      .owl-next {
        left: -45px;
      }
    }
  }
}
/*------ category end ------*/







/*--- card start ---*/
.card {
  position: relative;
  width: 220px;
  background-color: rgb(255, 255, 255);
  text-align: center;
  transition: all .5s ease-in-out;
  .card-img {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        // transform: scale(0.940);
      }
    }
  }
  .card-body {
    margin: 0 0 0 0;
    padding: 0 18px;
    .card-title {
      font-family: ar-reg;
      font-size: 15px;
      color: rgb(54, 53, 57);
      // min-height: 55px;
      display: block;
      height: 47px;
      overflow: hidden;
    }
    .card-price {
      padding: 0 0 20px 0;
      .new {
        font-family: ar-bold;
        font-size: 18px;
        color: #363539;
      }
      .old {
        font-family: ar-md;
        font-size: 16px;
        color: #afafaf;
        text-decoration: line-through;
      }
    }
  }
  .btns {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .5s ease-in-out;
  }
  .add-to-card {
    background-image: linear-gradient(90deg, #8a8a8a, #8a8a8a, #363539, #363539);
    background-size: 300%;
    width: 156px;
    height: 32px;
    outline: none;
    border: none;
    padding: 0;
    border-radius: 50px;
    color: rgb(255, 255, 255);
    font-family: ar-md;
    font-size: 14px;
    transition: all .5s ease-in-out;
    span {
      font-size: 16px;
      margin: 0 0 0 5px;
      
    }
    &:hover {
      background-position-x: 100%;
      &::before {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
      }
    }
  }
  .add-to-fav {
    border: none;
    outline: none;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-image: linear-gradient(90deg, #8a8a8a, #8a8a8a, #363539, #363539);
    background-size: 300%;
    font-size: 16px;
    color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px 0 0;
    transition: all .5s ease-in-out;
    &:hover {
      background-position-x: 100%;
    }
  }
  &:hover {
    box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.1);
    .btns {
      top: 40%;
      opacity: 1;
      visibility: visible;
    }
  }
}
/*--- card end ---*/



