/*------ responsive ------*/
@media (max-width: 1920px) {
  .owl-carousel .owl-item img {
    width: 100%;
  }
}
@media (max-width: 992px) {
  .nav-bar .zx-container {
    height: unset;
  }
  .nav-bar .sec-one .logo {
    width: 120px;
  }
  .nav-bar .sec-one .social {
    display: none;
  }
  .nav-bar .sec-two {
    display: none;
  }
  .nav-bar .sec-three {
    display: none;
  }
  .sec-four {
    display: block;
    padding: 0 5px;
    button {
      border: none;
      outline: none;
      background-color: transparent;
      color: #000;
      font-size: 22px;
      padding: 0 9px;
    }
  }
  .nav-bar .icons {
    display: none;
  }
  .fixed-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 999;
    .menu {
      width: 260px;
      height: 100%;
      position: absolute;
      top: 0;
      right: -280px;
      background-color: #fff;
      padding: 20px 20px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      &.show {
        right: 0;
      }
    }
    .menu-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .close {
        opacity: 1;
        color: #444;
        font-size: 16px;
        position: absolute;
        top: 20px;
        left: 15px;
      }
    }
    .main-list {
      margin: 35px 0px;
      transform: translateX(50%);
      transition: all .5s ease-in-out;
      .slide {
        cursor: pointer;
        position: relative;
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 8px;
          left: 10px;
          transition: all .5s ease-in-out;
        }
        &.active {
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-md;
        font-size: 16px;
        color: #444;
        margin: 7px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
      &.show {
        transform: translateX(0%);
      }
    }
    .sub-list {
      padding: 5px 10px;
      display: none;
      .slide-two {
        cursor: pointer;
        position: relative;
        
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 9px;
          left: 15px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #b39861;
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-reg;
        font-size: 16px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
    .sub-list-copy {
      padding: 5px 15px;
      display: none;
      li {
        font-family: ar-reg;
        font-size: 15px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
  }
  .fixed-menu .menu-header .lang-switcher {
    a {
      font-family: ar-reg;
      font-size: 15px;
      color: #000;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
    button {
      border: none;
      outline: none;
      background-color: transparent;
      padding: 0 0 0 5px;
    }
  }
  .header {
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        bottom: 20px;
        .owl-dot {
          margin: 0 5px;
        }
      }
    }
  }
  .header .owl-carousel .owl-nav {
    display: none;
  }
  .category .owl-carousel .owl-nav {
    display: none;
  }
  .category {
    margin: 0px 0;
    .cat {
      width: max-content;
