      margin: 0px auto;
    }
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: -25px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: rgba(0, 0, 0, 0.502);
          border-radius: 50%;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: #000;
          }
        }
      }
    }
  }
  .card .card-body .card-title {
    padding: 0 15px;
  }
  .sp-offers {
    
