      }
    }
  }
  .co-worker {
    padding: 30px 0;
    .company {
      margin: 0 auto;
    }
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: -10px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
