          background-color: rgba(0, 0, 0, 0.502);
          border-radius: 50%;
          outline: none;
          margin: 0 5px;
          &.active {
            background-color: #000;
          }
        }
      }
    }
  }
  .servs .zx-container {
    height: auto;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10px 0;
  }
  .serv {
    padding: 15px 0;
  }
  .footer {
    margin: 30px 0 0 0;
    padding: 30px 10px 30px 10px;
    .footer-img {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
    }
    .footer-info {
      display: block;
      text-align: center;
      padding: 0 10px;
      margin: 40px 0 40px 0;
      li {
        text-align: center;
        a {
          width: max-content;
          margin: 0 auto;
        }
      }
    }
    .list-header {
      margin: 15px 0;
      position: relative;
      &::after {
        content: "";
        font-family: "Linearicons-Free";
        font-size: 12px;
        color: #fff;
        position: absolute;
        top: 50%;
        left: 0px;
        transform: translateY(-50%);
        transition: all .5s ease-in-out;
      }
      &.active {
        &::after {
          content: "";
        }
      }
    }
    ul {
      display: none;
