html[dir="ltr"] {
  .nav-bar .sec-one .social {
    border-right: unset;
    border-left: 1px solid #dcdede;
  }
  .nav-bar .sec-two ul li {
    border-left: unset;
    border-right: 1px solid #f2f2f2;
    &:last-child {
      border: none;
    }
  }
  .nav-bar .sec-three {
    margin: 0 50px 0 0;
  }
  .nav-bar .icons {
    left: unset;
    right: 0;
  }
  .search .search-icon {
    left: unset;
    right: 15px;
  }
  .card .add-to-card span {
    margin: 0 5px 0 0;
  }
  .card .add-to-fav {
    margin: 0 0 0 5px;
  }
  .footer .footer-info i {
    padding: 0 5px 0 0;
  }
  .footer .subscribe button {
    left: unset;
    right: 20px;
  }

  @media (max-width: 992px) {
    .fixed-menu .menu-header .lang-switcher button {
      padding: 0 5px 0 0;
    }
    .fixed-menu .menu {
      left: -280px;
      right: unset;
      &.show {
        left: 0;
        right: unset;
      }
    }
    .fixed-menu .menu-header .close {
      left: unset;
      right: 15px;
    }
    .fixed-menu .menu-header .lang-switcher {
      right: unset;
      left: 0;
    }
    .fixed-menu .menu-header .lang-switcher .switch {
      left: 65px;
    }
    .fixed-menu .main-list .slide::after {
      left: unset;
      right: 10px;
    }
    .fixed-menu .main-list .slide.active::after {
      content: "";
    }
    .footer .list-header::after {
      left: unset;
      right: 0;
    }
    .footer .list-header.active {
      &::after {
        content: "";
      }
    }
  }
}